@font-face {
  font-family: "SF Pro Text";
  src: url("../fonts/SFProText-Regular.woff2") format("woff2");
  font-weight: normal; /* Regular */
  display: swap;
}
@font-face {
  font-family: "SF Pro Text";
  src: url("../fonts/SFProText-Bold.woff2") format("woff2");
  font-weight: bold; /* Bold */
  display: swap;
}
/* Document
 * ========================================================================== */
/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */
html {
  line-height: 1.15; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
 * ========================================================================== */
/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Edge, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
 * ========================================================================== */
/**
 * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
 */
dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}

/**
 * Remove the margin on nested lists in Edge 18- and IE.
 */
ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}

/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Correct the inheritance of border color in Firefox.
 * 3. Show the overflow in Edge 18- and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  color: inherit; /* 2 */
  height: 0; /* 1 */
  overflow: visible; /* 3 */
}

/**
 * Add the correct display in IE.
 */
main {
  display: block;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
 * ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * Add the correct text decoration in Edge 18-, IE, and Safari.
 */
abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/* Embedded content
 * ========================================================================== */
/**
 * Add the correct display in IE 9-.
 */
audio,
video {
  display: inline-block;
}

/**
 * Add the correct display in iOS 4-7.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Remove the border on images within links in IE 10-.
 */
img {
  border-style: none;
}

/**
 * Hide the overflow in IE.
 */
svg:not(:root) {
  overflow: hidden;
}

/* Tabular data
 * ========================================================================== */
/**
 * 1. Correct table border color inheritance in all Chrome, Edge, and Safari.
 * 2. Remove text indentation from table contents in Chrome, Edge, and Safari.
 */
table {
  border-color: inherit; /* 1 */
  text-indent: 0; /* 2 */
}

/* Forms
 * ========================================================================== */
/**
 * Remove the margin on controls in Safari.
 */
button,
input,
select {
  margin: 0;
}

/**
 * 1. Show the overflow in IE.
 * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
 */
button {
  overflow: visible; /* 1 */
  text-transform: none; /* 2 */
}

/**
 * Correct the inability to style buttons in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * Show the overflow in Edge 18- and IE.
 */
input {
  overflow: visible;
}

/**
 * 1. Correct the text wrapping in Edge 18- and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  white-space: normal; /* 1 */
}

/**
 * 1. Add the correct display in Edge 18- and IE.
 * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
 */
progress {
  display: inline-block; /* 1 */
  vertical-align: baseline; /* 2 */
}

/**
 * Remove the inheritance of text transform in Firefox.
 */
select {
  text-transform: none;
}

/**
 * 1. Remove the margin in Firefox and Safari.
 * 2. Remove the default vertical scrollbar in IE.
 */
textarea {
  margin: 0; /* 1 */
  overflow: auto; /* 2 */
}

/**
 * 1. Add the correct box sizing in IE 10-.
 * 2. Remove the padding in IE 10-.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * 1. Correct the odd appearance in Chrome, Edge, and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Safari.
 */
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
 * Correct the text style of placeholders in Chrome, Edge, and Safari.
 */
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/**
 * Remove the inner padding in Chrome, Edge, and Safari on macOS.
 */
::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style upload buttons in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/**
 * Remove the inner border and padding of focus outlines in Firefox.
 */
::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus outline styles unset by the previous rule in Firefox.
 */
:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Remove the additional :invalid styles in Firefox.
 */
:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
 * ========================================================================== */
/*
 * Add the correct display in Edge 18- and IE.
 */
details {
  display: block;
}

/*
 * Add the correct styles in Edge 18-, IE, and Safari.
 */
dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Scripting
 * ========================================================================== */
/**
 * Add the correct display in IE 9-.
 */
canvas {
  display: inline-block;
}

/**
 * Add the correct display in IE.
 */
template {
  display: none;
}

/* User interaction
 * ========================================================================== */
/**
 * Add the correct display in IE 10-.
 */
[hidden] {
  display: none;
}

html, body, html *, *, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

body.no-scroll {
  overflow: hidden;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}
.wrapper main {
  flex: 1;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}
button:disabled {
  cursor: unset;
}

span, b {
  color: inherit;
  font: inherit;
}

li {
  list-style: none;
}

input, textarea {
  outline: none;
  border: none;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

[class*=icon-]::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
}

* {
  scrollbar-color: #4286F4 #FFF;
  scrollbar-width: thin;
}

body, html {
  font-family: "Manrope", sans-serif;
}

[class*=__container], .container {
  padding: 0 16px;
  margin: 0 auto;
  max-width: clamp(944px, -0.036rem + 81.43vw, 1172px);
  min-width: 240px;
  width: 100%;
  flex: 1 0 auto;
}

.nav__list {
  display: flex;
  gap: 24px;
}
.nav__item {
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .nav__item:hover {
    color: #444444;
  }
}

.nav-cta {
  display: flex;
}
.nav-cta__link {
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .nav-cta__link:hover {
    color: #444444;
  }
}
.nav-cta ._line {
  display: block;
  margin: 1px 16px; /* Line 3 */
  width: 0px;
  border: 1px solid #444444;
}

.title {
  font-weight: 700;
  font-size: 40px;
  line-height: 1.2;
  color: #1B1B1E;
}
@media (max-width: 1160px) {
  .title {
    font-size: 32px;
  }
}
@media (max-width: 640px) {
  .title {
    font-size: 24px;
  }
}

.subtitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.1;
  color: #1B1B1E;
}
@media (max-width: 640px) {
  .subtitle {
    font-size: 20px;
  }
}

@media (max-width: 440px) {
  .text-fill {
    background-color: #FEDC32;
  }
}

.section {
  position: relative;
  overflow: hidden;
  border-radius: 32px;
  margin-left: 16px;
  margin-right: 16px;
  margin-top: clamp(8px, 0.28rem + 0.8vw, 16px);
}
.section > * {
  position: relative;
}

.review-bg {
  position: absolute;
  transform: translate(-50%, -50%);
  min-width: max-content;
  min-height: max-content;
  pointer-events: none;
}

.review2-bg {
  position: absolute;
  transform: translate(-50%, -50%);
  min-width: max-content;
  min-height: max-content;
  pointer-events: none;
}

.button {
  display: flex;
  text-align: center;
  justify-content: center;
  background: #4286F4;
  border-radius: 8px;
  color: white;
  transition: 0.3s ease;
  border: 2px solid transparent;
  padding: 16px 20px;
}
.button span {
  font-weight: 600;
  font-size: 16px;
  line-height: 1.2;
  color: #FFFFFF;
}
.button svg path, .button svg rect, .button span {
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .button:hover {
    background-color: white;
    border-color: #4286F4;
  }
  .button:hover svg path, .button:hover svg rect {
    fill: #4286F4;
  }
  .button:hover span {
    color: #4286F4;
  }
}
@media (max-width: 1160px) {
  .button {
    padding: 14px 18px;
  }
  .button span {
    font-size: 14px;
  }
}
.button_white {
  background: white;
}
.button_white span {
  color: #4286F4;
}
.button_white svg path, .button_white svg rect {
  color: #4286F4;
}
@media (any-hover: hover) {
  .button_white:hover {
    background: transparent;
    border-color: #FFFFFF;
  }
  .button_white:hover span {
    color: white;
  }
  .button_white:hover svg path, .button_white:hover svg rect {
    color: white;
  }
}

.menu {
  display: flex;
  gap: 8px;
}
@media (max-width: 990px) {
  .menu {
    padding: 1px 0;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    height: 56px;
    align-items: center;
    background: #FFFFFF;
    border-top: 2px solid #E5E7EB;
    box-shadow: 0px -2px 24px rgba(0, 0, 0, 0.15);
  }
}

.menu-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1px;
  font-weight: 500;
  font-size: clamp(12px, 0.232rem + 0.71vw, 14px);
  line-height: 1.3;
  color: #797B7D;
  padding: 6px 1px;
}
.menu-btn_mobile {
  display: none;
}
.menu-btn span {
  flex: 1 0 auto;
}
.menu-btn svg {
  height: auto;
  width: clamp(20px, 0.214rem + 1.43vw, 24px);
}
.menu-btn svg path {
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .menu-btn:hover {
    color: #4286F4;
  }
  .menu-btn:hover svg path {
    fill: #4286F4;
  }
}
@media (max-width: 990px) {
  .menu-btn {
    flex: 1 0 auto;
    font-weight: 600;
  }
  .menu-btn_mobile {
    display: flex;
  }
  .menu-btn._active {
    color: #4286F4;
  }
  .menu-btn._active svg path {
    fill: #4286F4;
  }
  .menu-btn svg {
    height: auto;
    width: 32px;
  }
  .menu-btn span {
    font-size: 12px;
  }
  .menu-btn:nth-last-child(3) {
    order: 3;
  }
  .menu-btn:nth-last-child(1) {
    order: 3;
  }
  .menu-btn:nth-last-child(2) {
    order: 1;
  }
}
@media (max-width: 440px) {
  .menu-btn span {
    display: none;
  }
  .menu-btn svg {
    width: 24px;
  }
  .menu-btn._active {
    color: #797B7D;
  }
  .menu-btn._active svg path {
    fill: #797B7D;
  }
}

.rating {
  width: 160px;
  display: flex;
  align-items: center;
  padding: 2px 8px;
  height: clamp(48px, 0.929rem + 2.86vw, 56px);
  gap: 0px 4px;
  background: #E8F0FE;
  border-radius: 8px;
}
@media (max-width: 1160px) {
  .rating {
    width: 80px;
    flex-direction: column;
    align-items: flex-start;
  }
}
@media (max-width: 440px) {
  .rating {
    width: auto;
    flex-direction: row;
    align-items: center;
    height: 24px;
    gap: 8px;
    justify-content: center;
    order: -1;
  }
}
.rating__value {
  display: flex;
  align-items: center;
  gap: 4px;
}
@media (max-width: 1160px) {
  .rating__value {
    line-height: 10px;
  }
}
.rating__value svg {
  width: clamp(16px, -1.071rem + 2.86vw, 24px);
}
.rating__value span {
  font-weight: 700;
  font-size: clamp(14px, -0.161rem + 1.43vw, 18px);
  line-height: 1.33;
  color: #1B1B1E;
}
.rating__text {
  font-size: clamp(8px, -0.018rem + 0.71vw, 10px);
  line-height: 1.2;
  color: #828282;
}
@media (max-width: 440px) {
  .rating__text {
    font-size: 14px;
  }
}
.rating__text svg {
  vertical-align: middle;
  margin-left: 0.4em;
  display: inline-flex;
  height: 1em;
  width: auto;
}
@media (max-width: 440px) {
  .rating__text svg {
    height: 12px;
  }
}

.search {
  flex: 1 0 auto;
  height: clamp(48px, 0.929rem + 2.86vw, 56px);
  position: relative;
  padding: 12px 5px;
  gap: 16px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 10px 24px;
}
@media (max-width: 440px) {
  .search {
    height: 40px;
  }
}
.search__input {
  position: absolute;
  transition: 0.3s ease;
  inset: 0;
  font-size: 16px;
  padding: 12px 24px;
  padding-right: 128px;
  line-height: 1.5;
  color: #444444;
  background: #FFFFFF;
  border: 2px solid #4286F4;
  border-radius: 8px;
}
@media (any-hover: hover) {
  .search__input:hover {
    border-color: #797B7D;
  }
}
.search__input:focus {
  border-color: #444444;
}
.search__input::placeholder {
  color: #AAAAAA;
}
.search__btn {
  position: relative;
  display: flex;
}
.search__btn svg {
  width: auto;
  height: clamp(24px, -0.571rem + 2.86vw, 32px);
}
@media (any-hover: hover) {
  .search__btn svg path {
    transition: 0.3s ease;
  }
  .search__btn:hover svg path {
    fill: #797B7D;
  }
}

.catalog-burger-btn {
  position: relative;
  padding: 4.5px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: clamp(48px, 0.929rem + 2.86vw, 56px);
  aspect-ratio: 1/1;
  display: flex;
}
.catalog-burger-btn svg {
  position: absolute;
  transition: 0.2s ease;
}
.catalog-burger-btn svg:nth-child(2) {
  transform: scale(0);
  opacity: 0;
}
.catalog-burger-btn._active svg:nth-child(1) {
  transform: scale(0);
  opacity: 0;
}
.catalog-burger-btn._active svg:nth-child(2) {
  transform: scale(1);
  opacity: 1;
}
@media (max-width: 990px) {
  .catalog-burger-btn {
    display: none;
  }
}

.review {
  z-index: 2;
  font-family: "SF Pro Text", sans-serif;
  display: grid;
  grid-template-columns: auto 1fr;
  padding: clamp(8px, 0.1rem + 1vw, 16px) clamp(8px, 0.1rem + 1vw, 16px) 24px;
  border-radius: 16px;
  background: white;
  max-width: 555px;
  max-height: max-content;
  gap: 8px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.08), 0px 2px 24px rgba(0, 0, 0, 0.08);
}
.review__hide-text {
  display: none;
}
.review__ava img {
  display: flex;
  border-radius: 50%;
}
.review__title-wrap {
  display: flex;
  gap: 2px;
}
.review__title {
  font-weight: 700;
  font-size: 13px;
  line-height: 1.23;
  letter-spacing: -0.078px;
  color: #2A5885;
}
.review__time {
  font-size: 13px;
  line-height: 1.23;
  letter-spacing: -0.078px;
  color: #818C99;
}
.review__time._bottom {
  display: none;
  margin-top: 8px;
}
@media (max-width: 440px) {
  .review__time._bottom {
    display: block;
  }
  .review__time._top {
    display: none;
  }
}
.review__text {
  font-size: 13px;
  line-height: 1.23;
  letter-spacing: -0.078px;
  color: #1B1B1E;
}
.review__full-btn {
  display: none;
  position: relative;
  font-weight: 700;
  font-size: 13px;
  line-height: 1.23;
  letter-spacing: -0.078px;
  color: #2A5885;
  grid-column: 1/3;
  margin-left: -8px;
  margin-right: -8px;
  padding: 16px 8px;
  border-radius: 0 0 16px 16px;
  transition: 0.2s ease;
}
.review__full-btn::before {
  content: "";
  position: absolute;
  left: 8px;
  right: 8px;
  top: 0;
  height: 0;
  border-top: 1px solid #E5E7EB;
}
@media (max-width: 440px) {
  .review__full-btn {
    display: flex;
  }
}
@media (max-width: 880px) {
  .review {
    max-width: none;
  }
}
@media (max-width: 440px) {
  .review {
    padding-bottom: 0;
  }
  .review__hide-text {
    display: inline;
  }
  .review__show-text {
    display: none;
  }
  .review._full .review__hide-text {
    display: none;
  }
  .review._full .review__show-text {
    display: inline;
  }
}

.review2 {
  font-family: "Golos Text", sans-serif;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 16px;
  padding: 20px 24px 32px 16px;
  background: #FFFFFF;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.08), 0px 2px 24px rgba(0, 0, 0, 0.08);
  border-radius: 16px;
}
.review2__client-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}
.review2__client-info-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  text-align: center;
}
.review2__ava {
  border-radius: 100%;
}
.review2__name {
  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: -0.35px;
  color: #1B1B1E;
}
.review2__reput {
  font-weight: 500;
  font-size: 10px;
  line-height: 1.2;
  letter-spacing: -0.35px;
  color: #797B7D;
  display: flex;
  flex-direction: column;
}
.review2__reput b {
  color: #27AE60;
}
.review2__country {
  font-weight: 500;
  font-size: 10px;
  line-height: 1.2;
  letter-spacing: -0.35px;
  color: #797B7D;
}
.review2__top {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}
.review2__rating {
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  font-size: 12px;
  line-height: 1;
  color: #FFFFFF;
  display: inline-flex;
  align-items: center;
  padding: 2px 4px;
  gap: 2px;
  background: #CE2457;
  border-radius: 4px 4px 4px 0px;
}
